* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
button {
  cursor: pointer;
}
a {
  color: black !important;
}
a:hover {
  text-decoration: none !important;
}
.App {
  height: auto;
  min-height: 100vh;
  background-image: url(./Components//mainBg.jpeg);
  background-size: 100vw 350vh;
  /* background-repeat: no-repeat; */
}
/* NavBar */
nav {
  height: 80px;
  width: 100%;
  /* border-bottom: 1px solid black; */
  padding: 0 35px;
  background: tomato;
  color: white;
  box-shadow: 3px 3px 8px black;
  margin-bottom: 30px;
}
nav #navButtons {
  width: 200px;
  height: 40px;
}
#navButtons button {
  border-radius: 15px !important;
  font-weight: bolder;
  font-size: 0.9rem !important;
  border: none;
  width: 95px;
  height: 35px;
  padding: 1px 3px;
}
#navButtons button:hover {
  background: maroon;
  color: white;
  transition: 0.2s;
}
#loggedUser {
  width: 260px;
}
#loggedUser button {
  border-radius: 15px !important;
  font-weight: bolder;
  font-size: 0.9rem !important;
  border: none;
  width: 90px;
  height: 35px;
  padding: 1px 3px;
}
#loggedUser button:hover {
  background: maroon;
  color: white;
  transition: 0.2s;
}
.loggedInButtons {
  border-radius: 15px !important;
  font-weight: bolder;
  font-size: 0.9rem !important;
  border: none;
  width: 90px;
  height: 35px;
  padding: 1px 3px;
}
.loggedInButtons:hover {
  background: maroon;
  color: white;
  transition: 0.2s;
}
#toProfile {
  font-size: 1.3rem;
}
#toProfile:hover {
  color: white !important;
  transition: 0.2s;
}
/* PostInput */
#postInput {
  width: 700px;
  height: 150px;
  margin: 0 auto;
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: tomato;
  border-radius: 5px;
}
#postInput input {
  height: 30%;
  width: 95%;
  outline: none;
  padding: 0 5px;
  font-weight: bolder;
}
#postInput div {
  width: 95%;
}
#postInput div button {
  width: 110px;
  height: 32px;
  border-radius: 15px;
  border: none;
  font-weight: bold;
}
#postInput div button:hover {
  background: maroon;
  color: white;
  transition: 0.2s;
}
/* PostItem */
#postItem {
  width: 700px;
  height: 200px;
  background-color: white;
  border: 1px solid tomato;
  box-shadow: 8px 8px 14px tomato;
  border-radius: 10px;
  margin: 30px auto;
}
/* userProfile */

#userProfile {
  /* border: 1px solid gray; */
  background: tomato;
  border-radius: 10px;
  margin: 100px auto;
  width: 380px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  box-shadow: 4px 4px 5px grey;
  font-weight: bolder;
  color: white;
  text-shadow: 1px 1px 4px black;
}
#userProfile button {
  width: 200px;
  height: 35px;
  font-weight: bolder;
  border-radius: 15px;
  border: none;
}
#userProfile button:hover {
  background: maroon;
  color: white;
  transition: 0.2s;
}

#likeButton:hover {
  font-size: 1.2rem;
}
#likeButton:active {
  font-size: 1.3rem;
}
#deletePostButton {
  box-shadow: 4px 5px 5px tomato;
}
#footer {
  background: tomato;
  height: 50px;
  position: fixed;
  bottom: 0;
  padding: 0 40px;
}
#footer i {
  font-size: 2.7rem;
}
#footer p {
  margin-top: 15px;
  width: 130px;
}
/* Media Query */
@media only screen and (max-width: 430px) {
  #label h1 {
    font-size: 1.4rem;
  }
  nav {
    padding: 0 10px !important;
  }
  #navButtons {
    display: flex;
    flex-direction: column;
    align-items: flex-end !important;
    margin-bottom: 30px !important;
  }
  #navButtons button {
    height: 30px;
    margin-bottom: 7px;
  }
  #loggedUser {
    display: flex;
    flex-direction: column;
    align-items: flex-end !important;
    margin-bottom: 7px !important;
  }
  #loggedUser #toProfile {
    margin-right: 0 !important;
    margin-bottom: 3px !important;
  }
  #postInput {
    width: 90%;
  }
  #postItem {
    width: 90%;
    height: 300px;
  }
  #footer {
    padding: 0 20px;
  }
  #footer i {
    font-size: 2rem;
  }
}
